import axios from "axios";
import axiosThrottle from "axios-request-throttle";
// eslint-disable-next-line no-undef
const pendingResolvers = [];
const MAX_REQUESTS_COUNT = 5;
let concurrentCount = 0;

const fmpAxiosLimiter = axios.create();
axiosThrottle.use(fmpAxiosLimiter as any, { requestsPerSecond: 2 });

const arborAxiosLimiter = axios.create();
// Create interceptor to handle concurrent requests
arborAxiosLimiter.interceptors.request.use(function (config) {
  return new Promise((resolve) => {
    if (concurrentCount < MAX_REQUESTS_COUNT) {
      concurrentCount++;
      console.log("Concurrent count", concurrentCount);
      resolve(config);
    } else {
      pendingResolvers.push(() => resolve(config));
      console.log("Pending resolvers", pendingResolvers.length);
    }
  });
});

arborAxiosLimiter.interceptors.response.use(
  function (response) {
    concurrentCount = Math.max(0, concurrentCount - 1);
    if (pendingResolvers.length > 0) {
      console.log("Resolving pending resolver");
      pendingResolvers.shift()();
    }
    return Promise.resolve(response);
  },
  function (error) {
    concurrentCount = Math.max(0, concurrentCount - 1);
    if (pendingResolvers.length > 0) {
      console.log("Resolving pending resolver");
      pendingResolvers.shift()();
    }
    return Promise.reject(error);
  }
);

export { fmpAxiosLimiter, arborAxiosLimiter };
