import { ARBOR_CHAT_API, getFromLocalStorage } from "../taskpane/taskpane";
import { arborAxiosLimiter } from "../http/limiter";
import DataLoader from "dataloader";
import { uniq } from "lodash";

async function batchArborScreen(queries: string[]) {
  const apiKey = getFromLocalStorage("apiKey");
  const results = {};
  await Promise.all(
    uniq(queries).map(async (query) => {
      const response = await arborAxiosLimiter
        .post(
          `${ARBOR_CHAT_API}/api/v1/screener/stock-suggestions`,
          { query },
          {
            headers: {
              "Content-Type": "application/json",
              "X-API-KEY": apiKey,
            },
          }
        )
        .catch(() => null);
      if (response && response.data) {
        results[query] = response.data;
      }
    })
  );
  return queries.map((query) => results[query]);
}

export default new DataLoader((queries: string[]) => batchArborScreen(queries));
