/* global CustomFunctions */

import { ARBOR_CHAT_API, getFromLocalStorage, getFromWorkbookSettings } from "../taskpane/taskpane";
import { defaultLongAnswerPrompt, defaultShortAnswerPrompt } from "../taskpane/constants/categories";
import { arborAxiosLimiter } from "../http/limiter";
import arborScreenLoader from "../dataloaders/arborScreen";

/**
 * Screens stocks based on given criteria.
 * @customfunction ARBOR_SCREEN
 * @param {string} query The screening criteria
 * @returns {Promise<Excel.EntityCellValue>} An array of stock symbols that match the criteria
 */
export async function screen(query: string): Promise<Excel.EntityCellValue> {
  try {
    if (!query.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
    }
    const apiKey = getFromLocalStorage("apiKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const queriedAt = new Date();
    const data = await arborScreenLoader.load(query);

    const result: Excel.EntityCellValue = {
      type: Excel.CellValueType.entity,
      text: `Screen: ${query}`,
      properties: {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "screen",
        },
        answer: {
          type: Excel.CellValueType.array,
          elements: data.result.map((i) => [i.symbol, i.name]),
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query,
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: new Date().toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: queriedAt.toISOString(),
        },
      },
    };
    return result;
  } catch (error) {
    console.error(`Error in SCREENING function:`, error);
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Error in SCREENING function");
  }
}

/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_DEEP
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @param {boolean} [isLongAnswer] Whether to return a long answer
 * @returns {Promise<Excel.EntityCellValue | Excel.ErrorCellValue>} The answer to the query
 */
export async function arborDeep(
  ticker: string,
  query: string,
  isLongAnswer?: boolean
): Promise<Excel.EntityCellValue | Excel.ErrorCellValue> {
  try {
    if (!query.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
    }
    if (!ticker.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
    }
    const apiKey = getFromLocalStorage("apiKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }
    const provider = await getFromWorkbookSettings("modelProvider");

    let queryPrompt = isLongAnswer ? defaultLongAnswerPrompt : defaultShortAnswerPrompt;
    queryPrompt = queryPrompt.replace(/\{\{query\}\}/g, query);
    queryPrompt = queryPrompt.replace(/\{\{ticker\}\}/g, ticker);

    const requestBody = JSON.stringify({
      query: queryPrompt,
      provider: provider || "openai",
    });

    const queriedAt = new Date();
    const apiHost = `${ARBOR_CHAT_API}/api/v1/answer`;
    const response = await arborAxiosLimiter
      .post(apiHost, requestBody, {
        headers: {
          "Content-Type": "application/json",
          "X-API-KEY": apiKey,
        },
      })
      .catch((error) => {
        console.error(`Error in ANSWER function:`, error);
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
      });

    if (response.status >= 400) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = response.data;

    if (data.success && data.result?.answer) {
      const entity: Excel.EntityCellValue = {
        type: Excel.CellValueType.entity,
        text: data.result.answer,
        properties: {
          type: {
            type: Excel.CellValueType.string,
            basicValue: isLongAnswer ? "long" : "short",
          },
          success: {
            type: Excel.CellValueType.boolean,
            basicValue: true,
          },
          ticker: {
            type: Excel.CellValueType.string,
            basicValue: ticker,
          },
          query: {
            type: Excel.CellValueType.string,
            basicValue: query || "",
          },
          answer: {
            type: Excel.CellValueType.string,
            basicValue: data.result.answer || "",
          },
          answeredAt: {
            type: Excel.CellValueType.string,
            basicValue: new Date().toISOString() || "",
          },
          answerId: {
            type: Excel.CellValueType.string,
            basicValue: data.result.id || "",
          },
          queriedAt: {
            type: Excel.CellValueType.string,
            basicValue: queriedAt.toISOString() || "",
          },
          references: {
            type: Excel.CellValueType.string,
            basicValue: data.result.references.map((ref) => ref.url).join("\n"),
          },
        },
        basicType: Excel.RangeValueType.error,
        basicValue: "#VALUE!",
      };
      return entity;
    } else if (data.success) {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#Failed to answer`,
      };
      return errorCell;
    } else {
      const errorCell: Excel.ErrorCellValue = {
        type: Excel.CellValueType.error,
        basicValue: `#${data.error}`,
      };
      return errorCell;
    }
  } catch (error) {
    console.error(`Error in ANSWER function:`, error);
    const errorCell: Excel.ErrorCellValue = {
      type: Excel.CellValueType.error,
      basicValue: `#${error.message}`,
    };
    return errorCell;
  }
}

/**
 * Answers questions about a company based on its stock symbol in a short format.
 * @customfunction ARBOR_NUMBER
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @returns {Promise<Excel.EntityCellValue | Excel.ErrorCellValue>} The answer to the query
 */
export async function answerNumber(
  ticker: string,
  query: string
): Promise<Excel.EntityCellValue | Excel.ErrorCellValue> {
  try {
    if (!query.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
    }
    if (!ticker.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
    }
    const apiKey = getFromLocalStorage("apiKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    let context: string = "";
    let usedFast = true;
    let deepResponse;

    const fastAnswer = await arborFast(ticker, query).catch(() => null);
    if (fastAnswer) {
      context = fastAnswer;
    } else {
      usedFast = false;
      deepResponse = await arborDeep(ticker, query, false).catch((error) => {
        return {
          type: Excel.CellValueType.error,
          basicValue: `#${error.message}`,
        };
      });
      if (deepResponse.type === Excel.CellValueType.error) {
        return deepResponse;
      }
      context = deepResponse["properties"].answer.basicValue;
    }

    const apiHost = `${ARBOR_CHAT_API}/api/v1/screener/numeric-extract`;
    const response = await arborAxiosLimiter
      .post(
        apiHost,
        {
          query: query,
          context: context,
          stockSymbol: ticker,
        },
        {
          headers: {
            "Content-Type": "application/json",
            "X-API-KEY": apiKey,
          },
        }
      )
      .catch((error) => {
        console.error(`Error in ANSWER function:`, error);
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
      });
    const numberAnswer = response.data;

    if (!numberAnswer) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Failed to answer");
    }
    const entity: Excel.EntityCellValue = {
      type: Excel.CellValueType.entity,
      text: `${numberAnswer.result.answer}`,
      properties: {
        ...(deepResponse ? deepResponse["properties"] : {}),
        type: {
          type: Excel.CellValueType.string,
          basicValue: "number",
        },
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        ticker: {
          type: Excel.CellValueType.string,
          basicValue: ticker,
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query || "",
        },
        answer: {
          type: Excel.CellValueType.double,
          basicValue: numberAnswer.result.answer || 0,
        },
        reason: {
          type: Excel.CellValueType.string,
          basicValue: numberAnswer.result.reason || "",
        },
        queryType: {
          type: Excel.CellValueType.string,
          basicValue: usedFast ? "fast" : "deep",
        },
      },
      basicType: Excel.RangeValueType.error,
      basicValue: "#VALUE!",
    };
    return entity;
  } catch (error) {
    const errorCell: Excel.ErrorCellValue = {
      type: Excel.CellValueType.error,
      basicValue: `#${error.message}`,
    };
    return errorCell;
  }
}

/**
 * Answers factual questions about a company based on its stock symbol.
 * @customfunction ARBOR_FAST
 * @param {string} ticker The stock symbol of the company
 * @param {string} query The factual question to ask about the company
 * @returns {Promise<string>} The factual answer to the query
 */
export async function arborFast(ticker: string, query: string): Promise<string> {
  try {
    if (!query.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
    }
    if (!ticker.trim()) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Ticker is required");
    }
    const apiKey = getFromLocalStorage("apiKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const response = await arborAxiosLimiter
      .post(
        `${ARBOR_CHAT_API}/api/v1/screener/answer-factual`,
        {
          query: query,
          stockSymbol: ticker,
        },
        {
          headers: {
            "Content-Type": "application/json",
            "X-API-KEY": apiKey,
          },
        }
      )
      .catch((error) => {
        console.error(`Error in ANSWER function:`, error);
        throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
      });
    if (!response.data?.result) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Failed to answer");
    }
    return response.data.result;
  } catch (error) {
    if (error.status === 401) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
    }
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, "Unknown error in ANSWER_FACTUAL function");
  }
}

CustomFunctions.associate("ARBOR_SCREEN", screen);
CustomFunctions.associate("ARBOR_DEEP", arborDeep);
CustomFunctions.associate("ARBOR_NUMBER", answerNumber);
CustomFunctions.associate("ARBOR_FAST", arborFast);